
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import Tag from 'c%/Tag.vue'

export default{
mixins: [ common,globals ],
components: {
Tag,
Tab,
TabItem
},
data () {
return {
data_uri: 'homework_tasks',
data:{
data0: [],
data1: []
},
tab: 0,
pageIndex: 1000,
dataReady0: false,
dataReady1: false,
map_lesson_type: {0:'班级作业',1:'一对一作业',2:'一对多作业'}
}
},
methods: {
formatDate (time) {
return String(time).substring(0,10)
},
onItemClick (val) {
let map_data_uri = {0:'homework_tasks',1:'homework_completes'},
is_ready = 'dataReady'+val
this.data_uri = map_data_uri[val]

if(!this[is_ready]){
this.downCallback()
}
},
detail (item) {
this.$router.push({path:'./homework/'+item.ht_id,params:{id:item.ht_id}})
},
deal_data (response) {
if(this.tab==0){
this.data.data0 = response.body.data
this.dataReady0 = true
}else{
this.data.data1 = response.body.data.list
this.dataReady1 = true
}

this.mescroll.endBySize(1, 1)
}
},
computed: {
top () {
let top = this.headerHeight + 44
return top
},
style () {
let top = this.top
return `top:${top}px;height:calc(100% - ${top}px);position:fixed`
}
}
}



